            filter: drop-shadow(-1px 6px 3px rgba(50, 50, 0, 0.5));
    position: fixed;
    width: 100%;
    z-index: 2;
}

.navigation {
    -webkit-animation: pulse 5s infinite;
            animation: pulse 5s infinite;
    background-color: transparent;
    transition: background-color 1.4s;
}

.navigation.active {
    background-color: black
}

