}

.form__wrapper .sign-up-container {
    left: 0;
    width: 50%;
    opacity: 0;
    z-index: 1;
}

.form__wrapper .container.right-panel-active .sign-up-container {
    transform: translateX(100%);
    opacity: 1;
    z-index: 5;
    -webkit-animation: show 0.6s;
            animation: show 0.6s;
}

@-webkit-keyframes show {
    0%,
    49.99% {
        opacity: 0;
        z-index: 1;
    }
    50%,
    100% {
        opacity: 1;
        z-index: 5;
    }
}

@keyframes show {
    0%,
    49.99% {
        opacity: 0;
        z-index: 1;
    }
    50%,
    100% {
        opacity: 1;
        z-index: 5;
    }
}

.form__wrapper .overlay-container {
    position: absolute;
    top: 0;
    left: 50%;
    width: 50%;
    overflow: hidden;
    transition: transform 0.6s ease-in-out;
    z-index: 100;
}

@media only screen and (min-width: 600px) {
    .form__wrapper .overlay-container {
        height: 100%;
    }
}

.form__wrapper .container.right-panel-active .overlay-container {
    transform: translateX(-100%);
}

.form__wrapper .overlay {
    background: #FF416C;
    background: linear-gradient(to right, #FF4B2B, #FF416C);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 0;
    color: #FFFFFF;
    position: relative;
    left: -100%;
    height: 100%;
    width: 200%;
    transform: translateX(0);
    transition: transform 0.6s ease-in-out;
}

.form__wrapper .container.right-panel-active .overlay {
    transform: translateX(50%);
}

.form__wrapper .overlay-panel {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0 40px;
    text-align: center;
    top: 0;
    height: 100%;
    width: 50%;
    transform: translateX(0);
    transition: transform 0.6s ease-in-out;
}

.form__wrapper .overlay-left {
    transform: translateX(-20%);
}

.form__wrapper .container.right-panel-active .overlay-left {
    transform: translateX(0);
}

.form__wrapper .overlay-right {
    right: 0;
    transform: translateX(0);
}

.form__wrapper .container.right-panel-active .overlay-right {
    transform: translateX(20%);
}

